<template>
  <div class="thought-garden-final">
    <!-- 背景层 -->
    <div class="garden-background">
      <img src="/assets/images/sky-gradient.png" alt="" class="sky-layer">
      <img src="/assets/images/soil-texture.png" alt="" class="soil-layer">
    </div>

    <!-- 主游戏容器 -->
    <div class="game-container">
      
      <!-- 开始界面 -->
      <div v-if="gameStage === 'start'" class="start-screen">
        <div class="welcome-card">
          <h1 class="garden-title">🌱 思维花园</h1>
          <p class="garden-subtitle">让我们一起种下一颗种子，看它如何成长...</p>
          <button @click="startJourney" class="start-button">
            <img src="/assets/images/seed.png" alt="种子" class="button-icon">
            开始种植
          </button>
        </div>
      </div>

      <!-- 主要游戏区域 -->
      <div v-else class="main-game-area">
        
        <!-- 植物展示区 -->
        <div class="plant-display-zone">
          <div class="plant-container" @click="handlePlantInteraction">
            
            <!-- 粒子效果层 -->
            <div class="particle-effects">
              <!-- 成长光点 -->
              <div v-if="plantStage >= 1" class="growth-sparkles">
                <div v-for="i in 8" :key="'growth-' + i" 
                     class="growth-sparkle"
                     :style="getGrowthSparkleStyle(i)"></div>
              </div>
              
              <!-- 花粉效果 -->
              <div v-if="showPollen" class="pollen-effects">
                <div v-for="i in 15" :key="'pollen-' + i" 
                     class="pollen-particle"
                     :style="getPollenStyle(i)"></div>
              </div>
            </div>

            <!-- 植物图片层 -->
            <div class="plant-images">
              <!-- 种子阶段 -->
              <transition name="plant-appear">
                <img v-if="plantStage >= 1" 
                     src="/assets/images/seed.png" 
                     alt="种子" 
                     class="plant-stage seed-image"
                     :class="{ 'stage-active': plantStage === 1 }">
              </transition>
              
              <!-- 幼芽阶段（深色带刺） -->
              <transition name="plant-grow">
                <img v-if="plantStage >= 2" 
                     src="/assets/images/sprout-dark.png" 
                     alt="深色幼芽" 
                     class="plant-stage sprout-dark-image"
                     :class="{ 'stage-active': plantStage === 2, 'thorny': showThorns }">
              </transition>
              
              <!-- 健康幼芽阶段 -->
              <transition name="plant-transform">
                <img v-if="plantStage >= 3" 
                     src="/assets/images/sprout-healthy.png" 
                     alt="健康幼芽" 
                     class="plant-stage sprout-healthy-image"
                     :class="{ 'stage-active': plantStage === 3 }">
              </transition>
              
              <!-- 花苞阶段 -->
              <transition name="plant-bud">
                <img v-if="plantStage >= 4 && !isFullyBloomed" 
                     :src="getFlowerBudImage()" 
                     alt="花苞" 
                     class="plant-stage flower-bud-image"
                     :class="{ 'stage-active': plantStage === 4 }">
              </transition>
              
              <!-- 盛开花朵阶段 -->
              <transition name="flower-bloom">
                <img v-if="isFullyBloomed" 
                     :src="getFlowerBloomImage()" 
                     alt="盛开花朵" 
                     class="plant-stage flower-bloom-image blooming">
              </transition>
            </div>

            <!-- 植物状态提示 -->
            <div class="plant-status-hint">
              <transition name="hint-fade">
                <p v-if="currentHint" class="status-text">{{ currentHint }}</p>
              </transition>
            </div>
          </div>
        </div>

        <!-- 交互输入区域 -->
        <div class="interaction-zone">
          
          <!-- 步骤1: 情景描述 -->
          <div v-if="gameStage === 'situation'" class="input-card">
            <div class="card-background">
              <img src="/assets/images/input-bg.png" alt="" class="bg-texture">
            </div>
            <div class="card-content">
              <h3 class="input-title">🌱 发生了什么事情？</h3>
              <p class="input-subtitle">描述一下让你困扰的情况，就像种下一颗种子...</p>
              <textarea 
                v-model="formData.situation"
                @input="handleSituationInput"
                class="game-input"
                placeholder="今天..."
                rows="3">
              </textarea>
              <div class="input-actions">
                <button @click="proceedToNextStep" 
                        :disabled="!canProceed"
                        class="proceed-btn">
                  继续 →
                </button>
              </div>
            </div>
          </div>

          <!-- 步骤2: 自动思维 -->
          <div v-if="gameStage === 'thought'" class="input-card">
            <div class="card-background">
              <img src="/assets/images/input-bg.png" alt="" class="bg-texture">
            </div>
            <div class="card-content">
              <h3 class="input-title">🌿 你的第一反应是什么？</h3>
              <p class="input-subtitle">那个瞬间冒出的想法，让幼芽破土而出...</p>
              <textarea 
                v-model="formData.automaticThought"
                @input="handleThoughtInput"
                class="game-input"
                placeholder="我觉得..."
                rows="3">
              </textarea>
              <div class="input-actions">
                <button @click="goToPreviousStep" class="back-btn">← 返回</button>
                <button @click="proceedToNextStep" 
                        :disabled="!canProceed"
                        class="proceed-btn">
                  继续 →
                </button>
              </div>
            </div>
          </div>

          <!-- 步骤3: 重新思考 -->
          <div v-if="gameStage === 'alternative'" class="input-card">
            <div class="card-background">
              <img src="/assets/images/input-bg.png" alt="" class="bg-texture">
            </div>
            <div class="card-content">
              <h3 class="input-title">✨ 有没有其他角度？</h3>
              <p class="input-subtitle">试试更温和、更平衡的视角，让植物变得健康...</p>
              <textarea 
                v-model="formData.alternativeThought"
                @input="handleAlternativeInput"
                class="game-input"
                placeholder="也许..."
                rows="3">
              </textarea>
              <div class="input-actions">
                <button @click="goToPreviousStep" class="back-btn">← 返回</button>
                <button @click="proceedToNextStep" 
                        :disabled="!canProceed"
                        class="proceed-btn">
                  继续 →
                </button>
              </div>
            </div>
          </div>

          <!-- 步骤4: 情绪选择 -->
          <div v-if="gameStage === 'feeling'" class="input-card feeling-card">
            <div class="card-background">
              <img src="/assets/images/input-bg.png" alt="" class="bg-texture">
            </div>
            <div class="card-content">
              <h3 class="input-title">🌸 现在感觉怎么样？</h3>
              <p class="input-subtitle">选择你的感受，看花朵如何绽放...</p>
              
              <!-- 情绪图标选择 -->
              <div class="emotion-grid">
                <div v-for="emotion in emotionOptions" 
                     :key="emotion.name"
                     @click="selectEmotion(emotion)"
                     class="emotion-option"
                     :class="{ 'selected': selectedEmotion?.name === emotion.name }">
                  <img :src="emotion.icon" :alt="emotion.name" class="emotion-icon">
                  <span class="emotion-label">{{ emotion.name }}</span>
                </div>
              </div>
              
              <!-- 强度滑块 -->
              <div v-if="selectedEmotion" class="intensity-section">
                <label class="intensity-label">感受强度: {{ formData.intensity }}%</label>
                <input 
                  type="range" 
                  min="10" 
                  max="100" 
                  v-model="formData.intensity"
                  @input="updateFlowerAppearance"
                  class="intensity-slider">
                <div class="intensity-labels">
                  <span>轻微</span>
                  <span>强烈</span>
                </div>
              </div>
              
              <div class="input-actions">
                <button @click="goToPreviousStep" class="back-btn">← 返回</button>
                <button @click="completeJourney" 
                        :disabled="!selectedEmotion"
                        class="complete-btn">
                  🌸 完成栽培
                </button>
              </div>
            </div>
          </div>

          <!-- 完成界面 -->
          <div v-if="gameStage === 'complete'" class="completion-card">
            <div class="card-background">
              <img src="/assets/images/card-border.png" alt="" class="border-decoration">
            </div>
            <div class="completion-content">
              <h2 class="completion-title">🎉 你的花朵绽放了！</h2>
              <p class="completion-message">这朵美丽的花代表了你的内心成长</p>
              
              <div class="completion-actions">
                <button @click="generateGardenCard" class="action-btn primary">
                  📸 保存花园图鉴
                </button>
                <button @click="startNewJourney" class="action-btn secondary">
                  🌱 种植新花朵
                </button>
                <button @click="viewProgress" class="action-btn tertiary">
                  📊 查看成长记录
                </button>
              </div>
            </div>
          </div>

        </div>

        <!-- 温馨提示 -->
        <div class="gentle-guidance" v-if="showGuidance">
          <div class="guidance-bubble">
            <p>{{ currentGuidance }}</p>
          </div>
        </div>

      </div>
    </div>

    <!-- 花园图鉴卡片模态框 -->
    <div v-if="showGardenCard" class="card-modal" @click="closeGardenCard">
      <div class="garden-card-container" @click.stop>
        
        <!-- 卡片装饰边框 -->
        <div class="card-border-decoration">
          <img src="/assets/images/card-border.png" alt="" class="border-frame">
        </div>
        
        <div class="garden-card-content" ref="gardenCardContent">
          <div class="card-header">
            <h2>🌸 我的思维花朵</h2>
            <span class="card-date">{{ formatDate(new Date()) }}</span>
          </div>
          
          <div class="card-flower-display">
            <img :src="getFlowerBloomImage()" alt="我的花朵" class="card-flower-image">
          </div>
          
          <div class="card-journey-summary">
            <div class="journey-item">
              <strong>🌱 种下的种子:</strong>
              <p>{{ formData.situation || '一个特别的时刻' }}</p>
            </div>
            <div class="journey-item">
              <strong>🌿 初始的想法:</strong>
              <p>{{ formData.automaticThought || '内心的声音' }}</p>
            </div>
            <div class="journey-item">
              <strong>✨ 新的理解:</strong>
              <p>{{ formData.alternativeThought || '更温和的视角' }}</p>
            </div>
            <div class="journey-item">
              <strong>🌸 最终的感受:</strong>
              <p>{{ selectedEmotion?.name || '平静' }} ({{ formData.intensity }}%)</p>
            </div>
          </div>
          
          <div class="card-blessing">
            <p>💝 每一次成长都值得被记录和珍藏</p>
          </div>
        </div>
        
        <div class="card-actions">
          <button @click="downloadGardenCard" class="download-btn">
            💾 保存到相册
          </button>
          <button @click="shareGardenCard" class="share-btn">
            📤 分享
          </button>
          <button @click="closeGardenCard" class="close-btn">
            关闭
          </button>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'ThoughtGardenFinal',
  data() {
    return {
      // 游戏状态
      gameStage: 'start', // start, situation, thought, alternative, feeling, complete
      plantStage: 0, // 0-4
      showThorns: false,
      showPollen: false,
      isFullyBloomed: false,
      showGardenCard: false,
      showGuidance: true,
      
      // 选择状态
      selectedEmotion: null,
      
      // 表单数据
      formData: {
        situation: '',
        automaticThought: '',
        alternativeThought: '',
        intensity: 70,
        timestamp: null
      },
      
      // 情绪选项（使用真实图片）
      emotionOptions: [
        { 
          name: '平静', 
          icon: '/assets/images/emotion-calm.png',
          color: '#84A98C',
          flowerType: 'blue'
        },
        { 
          name: '愉悦', 
          icon: '/assets/images/emotion-happy.png',
          color: '#FFC857',
          flowerType: 'yellow'
        },
        { 
          name: '希望', 
          icon: '/assets/images/emotion-hope.png',
          color: '#FF9B85',
          flowerType: 'red'
        },
        { 
          name: '安详', 
          icon: '/assets/images/emotion-peaceful.png',
          color: '#CAD2C5',
          flowerType: 'blue'
        }
      ],
      
      // 提示和引导
      guidanceMessages: {
        situation: '💭 描述完情况后，点击植物或按继续键...',
        thought: '🌱 写下想法，看幼芽如何破土而出...',
        alternative: '✨ 重新思考时，注意植物的神奇变化...',
        feeling: '🌸 选择情绪，见证花朵绽放的奇迹...'
      },
      
      hintMessages: {
        1: '种子已经种下，静待破土 🌱',
        2: '幼芽破土而出，带着尖锐的想法 🌿',
        3: '植物开始茁壮成长，变得更加健康 🍃',
        4: '花苞正在形成，即将绽放 🌸'
      }
    }
  },
  computed: {
    canProceed() {
      switch (this.gameStage) {
        case 'situation': return this.formData.situation.trim().length > 5
        case 'thought': return this.formData.automaticThought.trim().length > 3
        case 'alternative': return this.formData.alternativeThought.trim().length > 3
        case 'feeling': return this.selectedEmotion !== null
        default: return false
      }
    },
    
    currentHint() {
      return this.hintMessages[this.plantStage] || ''
    },
    
    currentGuidance() {
      return this.guidanceMessages[this.gameStage] || ''
    }
  },
  methods: {
    // 游戏流程控制
    startJourney() {
      this.gameStage = 'situation'
      this.plantStage = 1
      this.formData.timestamp = new Date()
    },
    
    proceedToNextStep() {
      if (!this.canProceed) return
      
      switch (this.gameStage) {
        case 'situation':
          this.gameStage = 'thought'
          this.plantStage = 2
          this.showThorns = true
          break
        case 'thought':
          this.gameStage = 'alternative'
          this.plantStage = 3
          this.showThorns = false
          break
        case 'alternative':
          this.gameStage = 'feeling'
          this.plantStage = 4
          break
      }
    },
    
    goToPreviousStep() {
      switch (this.gameStage) {
        case 'thought':
          this.gameStage = 'situation'
          this.plantStage = 1
          this.showThorns = false
          break
        case 'alternative':
          this.gameStage = 'thought'
          this.plantStage = 2
          this.showThorns = true
          break
        case 'feeling':
          this.gameStage = 'alternative'
          this.plantStage = 3
          this.showThorns = false
          break
      }
    },
    
    completeJourney() {
      if (!this.selectedEmotion) return
      
      this.isFullyBloomed = true
      this.showPollen = true
      this.gameStage = 'complete'
      
      // 保存记录到store
      this.saveToStore()
      
      // 隐藏引导
      this.showGuidance = false
    },
    
    startNewJourney() {
      // 重置所有状态
      this.gameStage = 'start'
      this.plantStage = 0
      this.showThorns = false
      this.showPollen = false
      this.isFullyBloomed = false
      this.selectedEmotion = null
      this.showGuidance = true
      
      this.formData = {
        situation: '',
        automaticThought: '',
        alternativeThought: '',
        intensity: 70,
        timestamp: null
      }
    },
    
    // 交互处理
    handlePlantInteraction() {
      // 点击植物推进流程
      if (this.canProceed) {
        this.proceedToNextStep()
      }
    },
    
    handleSituationInput() {
      // 自动推进
      if (this.formData.situation.trim().length > 10) {
        setTimeout(() => {
          if (this.gameStage === 'situation') {
            this.proceedToNextStep()
          }
        }, 1500)
      }
    },
    
    handleThoughtInput() {
      if (this.formData.automaticThought.trim().length > 8) {
        setTimeout(() => {
          if (this.gameStage === 'thought') {
            this.proceedToNextStep()
          }
        }, 1500)
      }
    },
    
    handleAlternativeInput() {
      if (this.formData.alternativeThought.trim().length > 8) {
        setTimeout(() => {
          if (this.gameStage === 'alternative') {
            this.proceedToNextStep()
          }
        }, 1500)
      }
    },
    
    selectEmotion(emotion) {
      this.selectedEmotion = emotion
      this.updateFlowerAppearance()
      
      // 延迟完成，让用户看到变化
      setTimeout(() => {
        this.completeJourney()
      }, 2000)
    },
    
    updateFlowerAppearance() {
      // 花朵外观会根据情绪和强度变化
      // 这里可以添加更多视觉反馈
    },
    
    // 图片资源方法
    getFlowerBudImage() {
      if (!this.selectedEmotion) return '/assets/images/blue-flower-bud.png'
      return `/assets/images/${this.selectedEmotion.flowerType}-flower-bud.png`
    },
    
    getFlowerBloomImage() {
      if (!this.selectedEmotion) return '/assets/images/blue-flower-bloom.png'
      return `/assets/images/${this.selectedEmotion.flowerType}-flower-bloom.png`
    },
    
    // 粒子效果样式生成
    getGrowthSparkleStyle(index) {
      const angle = (index * 45) + (Math.random() * 30 - 15)
      const distance = 50 + Math.random() * 40
      const x = Math.cos(angle * Math.PI / 180) * distance
      const y = Math.sin(angle * Math.PI / 180) * distance
      const delay = Math.random() * 3
      const duration = 2 + Math.random() * 2
      
      return {
        left: `calc(50% + ${x}px)`,
        top: `calc(50% + ${y}px)`,
        animationDelay: `${delay}s`,
        animationDuration: `${duration}s`
      }
    },
    
    getPollenStyle(index) {
      const angle = (index * 24) + (Math.random() * 20 - 10)
      const distance = 30 + Math.random() * 50
      const x = Math.cos(angle * Math.PI / 180) * distance
      const y = Math.sin(angle * Math.PI / 180) * distance
      const delay = Math.random() * 2
      const duration = 4 + Math.random() * 2
      
      return {
        left: `calc(50% + ${x}px)`,
        top: `calc(50% + ${y}px)`,
        animationDelay: `${delay}s`,
        animationDuration: `${duration}s`
      }
    },
    
    // 花园图鉴相关
    generateGardenCard() {
      this.showGardenCard = true
    },
    
    closeGardenCard() {
      this.showGardenCard = false
    },
    
    async downloadGardenCard() {
      try {
        if (typeof html2canvas === 'undefined') {
          alert('图片生成功能暂不可用，请刷新页面重试')
          return
        }
        
        const canvas = await html2canvas(this.$refs.gardenCardContent)
        const link = document.createElement('a')
        link.download = `思维花园_${this.formatDate(new Date())}.png`
        link.href = canvas.toDataURL()
        link.click()
      } catch (error) {
        console.error('保存失败:', error)
        alert('保存失败，请稍后重试')
      }
    },
    
    shareGardenCard() {
      if (navigator.share) {
        navigator.share({
          title: '我的思维花园',
          text: '我刚刚完成了一次思维栽培之旅！',
          url: window.location.href
        })
      } else {
        // 备用分享方案
        alert('请使用下载功能保存图片后手动分享')
      }
    },
    
    // 数据保存
    saveToStore() {
      const record = {
        situation: this.formData.situation,
        automaticThought: this.formData.automaticThought,
        // 添加认知分析数据结构兼容
        supportingEvidence: '', // 暂时为空，保持兼容性
        contradictoryEvidence: '', // 暂时为空，保持兼容性
        alternativeThought: this.formData.alternativeThought,
        emotions: this.selectedEmotion ? [this.selectedEmotion.name] : [],
        intensity: this.formData.intensity,
        reevaluatedEmotions: this.selectedEmotion ? [this.selectedEmotion.name] : [],
        newEmotionIntensity: this.formData.intensity,
        timestamp: this.formData.timestamp || new Date(),
        // 花园特有数据
        emotion: this.selectedEmotion,
        flowerType: this.selectedEmotion?.flowerType,
        id: Date.now(),
        completed: true,
        gardenVersion: true // 标记为花园版本记录
      }
      
      // 集成到现有的store系统
      if (this.$store) {
        this.$store.state.thoughtRecords.unshift(record)
        this.$store.saveState()
      }
    },
    
    viewProgress() {
      // 跳转到记录页面
      this.$router.push('/dashboard')
    },
    
    formatDate(date) {
      return date.toLocaleDateString('zh-CN', {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      })
    }
  }
}
</script>

<style scoped>
.thought-garden-final {
  min-height: 100vh;
  position: relative;
  font-family: 'Inter', '方正宋刻本秀楷', sans-serif;
  overflow-x: hidden;
}

/* 背景层 */
.garden-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.sky-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 70%;
  object-fit: cover;
  opacity: 0.8;
}

.soil-layer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40%;
  object-fit: cover;
  opacity: 0.6;
}

/* 游戏容器 */
.game-container {
  position: relative;
  z-index: 1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem 1rem;
}

/* 开始界面 */
.start-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 80vh;
}

.welcome-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 30px;
  padding: 3rem 2rem;
  text-align: center;
  backdrop-filter: blur(15px);
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
  max-width: 500px;
}

.garden-title {
  font-size: 3rem;
  color: #52796F;
  margin-bottom: 1rem;
  font-weight: 300;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.1);
}

.garden-subtitle {
  font-size: 1.2rem;
