/* 关键帧动画 */
@keyframes sparkleFloat {
  0% {
    transform: translate(0, 0) scale(0);
    opacity: 0;
  }
  20% {
    transform: translate(0, 0) scale(1);
    opacity: 0.8;
  }
  50% {
    transform: translate(10px, -15px) scale(1.2);
    opacity: 0.4;
  }
  80% {
    transform: translate(-5px, -25px) scale(0.8);
    opacity: 0.6;
  }
  100% {
